





















































































import { Component, Vue } from "vue-property-decorator";
import api from "../../api";

@Component({})
export default class ApplicationQualificationAudit extends Vue {
  searchTDO: any = {
    current: 1,
    query: {
      appName: "",
      status: ""
    },
    size: 10
  };
  appList: any[] = [];
  loading: boolean = true;
  total: number = 0;

  handleCurrentChange(pageNum: number) {
    this.searchTDO.current = pageNum;
    this.getAppApplyList();
  }

  async getAppApplyList(newSearch?: boolean) {
    if (newSearch) {
      this.searchTDO.current = 1;
    }
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.getAppApplyList, this.searchTDO);
      if (status) {
        this.total = data.total;
        this.appList = data.records;
        for (let i in this.appList) {
          this.$set(this.appList[i], "isShow", false);
        }
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  async passTheAppApply(app: any) {
    this.$confirm("确认通过该应用申请?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.appPass + "?appId=" + app.id);
      if (status) {
        this.$message.success("已通过");
        this.getAppApplyList();
      } else {
        this.$message.error(message);
      }
    });
  }

  async rejectTheAppApply(app: any) {
    this.$prompt("请输入拒绝原因", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      inputErrorMessage: "原因不能为空",
      distinguishCancelAndClose: true,
      inputValidator: value => {
        return !!value;
      }
    }).then(async (data: any) => {
      let {
        data: { status, message }
      } = await this.$http.post(api.appReject, {
        businessId: app.id,
        result: data.value
      });
      if (status) {
        this.$message.success("操作成功");
        this.getAppApplyList();
      } else {
        this.$message.error(message);
      }
    });
  }

  created() {
    this.getAppApplyList();
  }
}
